import { Button, Checkbox, FormControlLabel, Stack, TextField, Typography } from '@mui/material'
import { DateTimePicker } from '@mui/x-date-pickers'
import { WorkflowAPI } from 'API/workflow'
import DialogConfirm from 'components/DialogConfirm'
import { IPage, IWebAuthor, PERMISSION } from 'interfaces'
import { useEffect, useMemo, useState } from 'react'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'

interface Props {
	canSelectPage: IPage[]
}
export default function Approval({ canSelectPage }: Props) {
	const dispatch = useAppDispatch()
	const [show, setShow] = useState(false)
	const [loading, setLoading] = useState(false)
	const [searchP, setSearchP] = useState('')
	const [searchDP, setSearchDP] = useState('')
	const [searchA, setSearchA] = useState('')

	const [hopeTime, setHopeTime] = useState(new Date(Math.ceil(new Date().getTime() / 3600000) * 3600000))

	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const [selectedPage, setSelectedPage] = useState<IPage[]>([])
	const [selectedDeletePage, setSelectedDeletePage] = useState<IPage[]>([])
	const [selectedAccount, setSelectedAccount] = useState<IWebAuthor[]>([])
	const webAuthors = useAppSelector((s) => s.WebAuthor.list)

	useEffect(() => {
		if (!show) {
			setSelectedAccount([])
			setSelectedDeletePage([])
			setSelectedPage([])
		}
	}, [show])

	const canSLP = useMemo(() => {
		return canSelectPage.filter(
			(f) =>
				f.active &&
				f.status !== '公開済' &&
				f.status !== '申請中' &&
				(f.url.includes(searchP) || f.title.includes(searchP)) &&
				!selectedDeletePage.find((a) => a.id === f.id)
		)
	}, [canSelectPage, searchP, selectedDeletePage])
	const canSLDP = useMemo(() => {
		return canSelectPage.filter(
			(f) =>
				f.active &&
				f.status !== '申請中' &&
				(f.status === '公開済' || canSelectPage.find((v) => v.id !== f.id && v.uuid === f.uuid)) &&
				(f.url.includes(searchDP) || f.title.includes(searchDP)) &&
				!selectedPage.find((a) => a.id === f.id)
		)
	}, [canSelectPage, searchDP, selectedPage])
	const canSLA = useMemo(() => {
		return webAuthors.filter((f) => f.email.includes(searchA))
	}, [webAuthors, searchA])
	if (!tokenInfo || tokenInfo.permissions[PERMISSION.CMS] < 2) return null
	const onSubmit = async () => {
		try {
			setLoading(true)
			const { error, message } = await WorkflowAPI.create({
				authorizers: selectedAccount.map((v) => v.id),
				pageIds: {
					public: selectedPage.map((v) => v.id),
					unPublic: selectedDeletePage.map((v) => v.id),
				},
				hopeTime: hopeTime.getTime(),
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			setShow(false)
		} finally {
			setLoading(false)
		}
	}
	return (
		<Stack>
			<Button
				variant="outlined"
				color="success"
				sx={{
					minWidth: 120,
				}}
				onClick={() => setShow(!show)}
			>
				承認申請
			</Button>
			<DialogConfirm
				open={show}
				loading={loading}
				handleClose={() => setShow(false)}
				title="承認申請"
				disabled={selectedAccount.length === 0 || (selectedPage.length === 0 && selectedDeletePage.length === 0)}
				primaryButtonText="申請"
				primaryButtonColor="success"
				component="form"
				maxWidth="xl"
				handleSubmit={onSubmit}
			>
				<Stack my={1} width={250}>
					<DateTimePicker
						label="公開予定"
						views={['year', 'month', 'day', 'hours', 'minutes']}
						inputFormat="yyyy年MM月dd日 HH:mm"
						value={hopeTime}
						minutesStep={5}
						onChange={(date) => {
							if (date) {
								setHopeTime(date)
							}
						}}
						minDate={new Date()}
						renderInput={(params) => <TextField {...params} size="small" />}
					/>
				</Stack>
				<Stack direction="row" gap={1} height="70vh">
					<Stack
						flex={1}
						sx={{
							border: '1px solid #ccc',
						}}
					>
						<Stack sx={{ bgcolor: '#0fa3', borderBottom: '1px solid #ccc', p: 1, overflow: 'auto' }} gap={1}>
							<Typography>公開申請ページ</Typography>
						</Stack>
						<Stack p={0.5}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={searchP}
								onChange={(e) => setSearchP(e.target.value)}
							/>
						</Stack>
						<Stack
							sx={{
								flex: 1,
								overflow: 'auto',
								px: 0.5,
							}}
						>
							{!searchP && (
								<Stack direction="row" alignItems="center" gap={0.5}>
									<FormControlLabel
										sx={{ px: 2 }}
										control={
											<Checkbox
												size="small"
												sx={{ p: 0 }}
												checked={selectedPage.length === canSLP.length}
												onChange={(e) => {
													const checked = e.target.checked
													if (checked) {
														setSelectedPage([...canSLP])
													} else {
														setSelectedPage([])
													}
												}}
											/>
										}
										label="全て"
									/>
								</Stack>
							)}
							{canSLP.map((v) => {
								return (
									<Stack direction="row" alignItems="center" gap={0.5} key={v.id}>
										<FormControlLabel
											sx={{ px: 2 }}
											control={
												<Checkbox
													size="small"
													sx={{ p: 0 }}
													checked={!!selectedPage.find((f) => f.id === v.id)}
													onChange={(e) => {
														const checked = e.target.checked
														if (checked) {
															setSelectedPage([...selectedPage, v])
														} else {
															const tmp = selectedPage.filter((f) => f.id !== v.id)
															setSelectedPage(tmp)
														}
													}}
												/>
											}
											label={v.url}
										/>
									</Stack>
								)
							})}
						</Stack>
					</Stack>
					<Stack
						flex={1}
						sx={{
							border: '1px solid #ccc',
						}}
					>
						<Stack sx={{ bgcolor: '#f205', borderBottom: '1px solid #ccc', p: 1, overflow: 'auto' }} gap={1}>
							<Typography>非公開申請ページ</Typography>
						</Stack>
						<Stack p={0.5}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={searchDP}
								onChange={(e) => setSearchDP(e.target.value)}
							/>
						</Stack>
						<Stack
							sx={{
								flex: 1,
								overflow: 'auto',
								px: 0.5,
							}}
						>
							{!searchDP && (
								<Stack direction="row" alignItems="center" gap={0.5}>
									<FormControlLabel
										sx={{ px: 2 }}
										control={
											<Checkbox
												size="small"
												sx={{ p: 0 }}
												checked={selectedDeletePage.length === canSLDP.length}
												onChange={(e) => {
													const checked = e.target.checked
													if (checked) {
														setSelectedDeletePage([...canSLDP])
													} else {
														setSelectedDeletePage([])
													}
												}}
											/>
										}
										label="全て"
									/>
								</Stack>
							)}
							{canSLDP.map((v) => {
								return (
									<Stack direction="row" alignItems="center" gap={0.5} key={v.id}>
										<FormControlLabel
											sx={{ px: 2 }}
											control={
												<Checkbox
													size="small"
													sx={{ p: 0 }}
													checked={!!selectedDeletePage.find((f) => f.id === v.id)}
													onChange={(e) => {
														const checked = e.target.checked
														if (checked) {
															setSelectedDeletePage([...selectedDeletePage, v])
														} else {
															const tmp = selectedDeletePage.filter((f) => f.id !== v.id)
															setSelectedDeletePage(tmp)
														}
													}}
												/>
											}
											label={v.url}
										/>
									</Stack>
								)
							})}
						</Stack>
					</Stack>
					<Stack
						flex={1}
						sx={{
							border: '1px solid #ccc',
						}}
					>
						<Stack sx={{ bgcolor: '#eee', borderBottom: '1px solid #ccc', p: 1, overflow: 'auto' }} gap={1}>
							<Typography>承認者</Typography>
						</Stack>
						<Stack p={0.5}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={searchA}
								onChange={(e) => setSearchA(e.target.value)}
							/>
						</Stack>
						<Stack
							sx={{
								flex: 1,
								overflow: 'auto',
								px: 0.5,
							}}
						>
							{!searchA && (
								<Stack direction="row" alignItems="center" gap={0.5}>
									<FormControlLabel
										sx={{ px: 2 }}
										control={
											<Checkbox
												size="small"
												sx={{ p: 0 }}
												checked={selectedAccount.length === canSLA.length}
												onChange={(e) => {
													const checked = e.target.checked
													if (checked) {
														setSelectedAccount([...canSLA])
													} else {
														setSelectedAccount([])
													}
												}}
											/>
										}
										label="全て"
									/>
								</Stack>
							)}
							{canSLA.map((v) => {
								return (
									<Stack direction="row" alignItems="center" gap={0.5} key={v.id}>
										<FormControlLabel
											sx={{ px: 2 }}
											control={
												<Checkbox
													size="small"
													sx={{ p: 0 }}
													checked={!!selectedAccount.find((f) => f.id === v.id)}
													onChange={(e) => {
														const checked = e.target.checked
														if (checked) {
															setSelectedAccount([...selectedAccount, v])
														} else {
															const tmp = selectedAccount.filter((f) => f.id !== v.id)
															setSelectedAccount(tmp)
														}
													}}
												/>
											}
											label={v.email}
										/>
									</Stack>
								)
							})}
						</Stack>
					</Stack>
				</Stack>
			</DialogConfirm>
		</Stack>
	)
}
