import FolderIcon from '@mui/icons-material/Folder'
import { Button, Divider, Stack, Typography } from '@mui/material'
import { WorkflowAPI } from 'API/workflow'
import PreviewButton from 'components/Button/Preview'
import { format } from 'date-fns'
import { IPage, PERMISSION } from 'interfaces'
import React, { useEffect, useMemo, useState } from 'react'
import { BsFiletypeHtml } from 'react-icons/bs'
import { FaRegStopCircle } from 'react-icons/fa'
import { MdEditOff } from 'react-icons/md'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector, useQuery } from 'redux/hooks'
import { getPages } from 'redux/slices/CMS/Page'
import { getTemplates } from 'redux/slices/CMS/Template'
import { UIAction } from 'redux/slices/UI'
import Approval from './Approval'
import { LoadingButton } from '@mui/lab'

export default function Page() {
	const { webId } = useParams<{ webId: string }>()
	const dispatch = useAppDispatch()
	const nav = useNavigate()
	const search = useQuery()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const pages = useAppSelector((s) => s.CMS.Page.list).filter((f) => webId && f.webId === parseInt(webId))
	const status = useAppSelector((s) => s.CMS.Page.status)
	const web = useAppSelector((s) => s.CMS.Web.list).find((f) => f.id === Number(webId))
	useEffect(() => {
		if (status === 'init') dispatch(getPages())
	}, [status, dispatch])
	const templateStatus = useAppSelector((s) => s.CMS.Template.status)
	const [loading, setLoading] = useState(false)

	useEffect(() => {
		if (templateStatus === 'init') dispatch(getTemplates())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (!webId) {
			nav(-1)
		}
	}, [webId, nav])

	const workflows = useAppSelector((s) => s.Workflow.list).filter((f) => f.sender === tokenInfo?.id)
	const currentPath = useAppSelector((s) => s.UI.currentPath)

	const [parent, setParent] = useState<string[]>([''])
	useEffect(() => {
		const p = search.get('p')
		if (!p) return setParent([''])
		setParent(p.split('/'))
	}, [search])

	const canSelectPage = useMemo(() => {
		return pages.filter((page) => {
			return !currentPath.find((f) => f.path === `/cms/${page.webId}/page/${page.id}`)
		})
	}, [currentPath, pages])

	const fixPages = useMemo(() => {
		const files: IPage[] = []
		const folders: string[] = []
		const reg = new RegExp('^' + parent.join('/') + '/', 'g')
		const active = pages.filter((f) => f.active).filter((f) => f.url.match(reg))
		active.forEach((v) => {
			if (v.url.split('/').length === parent.length + 1) {
				files.push(v)
			} else {
				const folder = v.url.split('/')[parent.length]
				if (!folders.includes(folder)) {
					folders.push(folder)
				}
			}
		})
		return {
			folders,
			files,
		}
	}, [pages, parent])
	if (!webId || !web || !tokenInfo) return null

	return (
		<Stack gap={1}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					ページ一覧管理
				</Typography>
				<Approval canSelectPage={canSelectPage} />
				{tokenInfo.permissions[PERMISSION.CMS] > 1 && (
					<Link to={`/cms/${webId}/page/create`}>
						<Button
							variant="outlined"
							sx={{
								minWidth: 120,
							}}
						>
							新規作成
						</Button>
					</Link>
				)}
			</Stack>
			<Divider />
			<Stack border="1px solid #eee" py={1}>
				<Stack direction="row" gap={0.1} alignItems="center" px={1}>
					<Typography width={60} fontWeight={600}>
						URL:
					</Typography>
					~
					<Typography
						onClick={() => {
							setParent([''])
						}}
						sx={{
							color: '#004cff',
							cursor: 'pointer',
							'&:hover': {
								color: '#0093ff',
								textDecoration: 'underline',
							},
						}}
					>
						{web.name}
					</Typography>
					/
					{parent.map((r, i) => {
						return (
							<React.Fragment key={i}>
								<Typography
									onClick={() => {
										setParent([...parent].slice(0, i + 1))
									}}
									sx={{
										color: '#004cff',
										cursor: 'pointer',
										'&:hover': {
											color: '#0093ff',
											textDecoration: 'underline',
										},
									}}
								>
									{r}
								</Typography>
								{!!r && '/'}
							</React.Fragment>
						)
					})}
				</Stack>
				<Divider sx={{ mb: 2, mt: 1 }} />
				{fixPages.folders.length > 0 && (
					<>
						<Stack direction="row" gap={2} flexWrap="wrap">
							{fixPages.folders.map((folder, i) => {
								return (
									<Stack
										key={i}
										direction="row"
										justifyContent="center"
										alignItems="center"
										gap={0.5}
										sx={{
											cursor: 'pointer',
											p: 1,
											userSelect: 'none',
											'&:hover': {
												bgcolor: '#eee6',
												boxShadow: '0 2px 4px rgba(0,0,0,0.3)',
											},
											'&:active': {
												bgcolor: '#eee',
												boxShadow: '0 2px 2px rgba(0,0,0,0.2)',
											},
										}}
										onClick={() => {
											setParent([...parent, folder])
											nav('./?p=' + [...parent, folder].join('/'))
										}}
									>
										<FolderIcon sx={{ color: '#eeb200', fontSize: 32, minWidth: 30 }} />
										<Typography fontWeight={600}>{folder}</Typography>
									</Stack>
								)
							})}
						</Stack>

						<Divider sx={{ my: 2 }} />
					</>
				)}
				<Stack
					direction="row"
					gap={1}
					sx={{
						borderBottom: '1px dashed #eee',
						py: 1,
						bgcolor: '#eee8',
						px: 1,
					}}
				>
					<Typography variant="body2" fontWeight={600} flex={1}>
						ファイル名
					</Typography>
					<Typography variant="body2" fontWeight={600} flex={1}>
						URL
					</Typography>
					<Typography variant="body2" fontWeight={600} width={145}>
						編集日付
					</Typography>
					<Typography variant="body2" fontWeight={600} width={100} textAlign="center">
						ステータス
					</Typography>
					<Typography variant="body2" fontWeight={600} width={100} textAlign="center">
						プレビュー
					</Typography>
					<Typography variant="body2" fontWeight={600} flex={1}>
						編集者
					</Typography>
				</Stack>
				{fixPages.files.map((file, i) => {
					const editor = currentPath.find((f) => f.path === `/cms/${file.webId}/page/${file.id}`)
					const fw = workflows.find((f) => f.status === 'created' && f.pageId === file.id)
					return (
						<Stack
							key={i}
							direction="row"
							gap={1}
							sx={{
								borderBottom: '1px dashed #eee',
								py: 0.5,
								px: 1,
								bgcolor: editor || file.status === '申請中' ? (fw ? '#cf33' : '#a403') : 'transparent',
								'&:hover': {
									bgcolor: editor || file.status === '申請中' ? '' : '#eee8',
								},
								position: 'relative',
							}}
						>
							{fw && (
								<Stack
									sx={{
										position: 'absolute',
										top: 0,
										right: 0,
										height: '100%',
									}}
									direction="row"
									alignItems="center"
								>
									<LoadingButton
										size="small"
										variant="contained"
										color="warning"
										loading={loading}
										sx={{
											display: 'flex',
											gap: 1,
											alignItems: 'center',
											p: 0,
											px: 1,
										}}
										onClick={async () => {
											try {
												setLoading(true)
												const { error, message } = await WorkflowAPI.remand({
													id: fw.id,
												})
												dispatch(
													UIAction.addGlobalMessage({
														type: error ? 'error' : 'success',
														message: message,
													})
												)
											} finally {
												setLoading(false)
											}
										}}
									>
										<FaRegStopCircle />
										取り下げ
									</LoadingButton>
								</Stack>
							)}
							<Stack direction="row" alignItems="center" gap={0.5} flex={1}>
								{tokenInfo.permissions[PERMISSION.CMS] < 2 || editor || file.status === '申請中' ? (
									<Typography variant="body2" flex={1}>
										<BsFiletypeHtml style={{ fontSize: 24, minWidth: 30 }} />
										{file.url.split('/').pop()}
										<MdEditOff style={{ fontSize: 20, minWidth: 30, color: '#f50' }} />
									</Typography>
								) : (
									<Link to={`/cms/${webId}/page/${file.id}`}>
										<BsFiletypeHtml style={{ fontSize: 24, minWidth: 30 }} />
										{file.url.split('/').pop()}
									</Link>
								)}
							</Stack>
							<Typography variant="body2" flex={1}>
								{file.url}
							</Typography>
							<Typography variant="body2" width={145}>
								{format(new Date(file.updatedAt), 'yyyy年MM月dd日 HH:mm')}
							</Typography>
							<Typography variant="body2" width={100} textAlign="center">
								{file.status}
							</Typography>
							<Link
								to={`${process.env.NODE_ENV === 'development' ? 'http://localhost:488' : ''}/staging/${file.web.name}${
									file.url
								}`}
								target="_blank"
							>
								<Typography variant="body2" width={100} textAlign="center">
									<PreviewButton variant="text" />
								</Typography>
							</Link>
							<Typography flex={1} variant="caption" mr={1}>
								{editor?.auth.email}
							</Typography>
						</Stack>
					)
				})}
			</Stack>
		</Stack>
	)
}
